import React from "react";
import { graphql, useStaticQuery} from "gatsby";

// Styles
import "./FarrowingHeader.styles.scss";

// Components
import PigIcon from "./PigIcon/PigIcon.component";

const FarrowingHeader = () => {

    const data = useStaticQuery(graphql`
        query {
            allMarkdownRemark(filter: {frontmatter: {title: {eq: "Farrowing"}}}) {
                edges {
                    node {
                        frontmatter {
                            title
                            subtitle
                        }
                        html
                    }
                }
            }
        }
    `)
    
    return (
        <div className="farrowing-header">
            {
                data.allMarkdownRemark.edges.map(({node}) => (
                    <div>
                        <h2>{ node.frontmatter.title }</h2>
                        <h3>{ node.frontmatter.subtitle } <PigIcon /><PigIcon /></h3>
                        <p dangerouslySetInnerHTML={{ __html: node.html }} />
                    </div>
                ))
            }
        </div>
    )
}

export default FarrowingHeader;