import React from "react";
import { graphql, useStaticQuery } from "gatsby";

// Styles
import "./FarrowingGraph.styles.scss";

// Components
import TonisityLogo from "../../TonisityLogo/TonisityLogo.component";
import FarrowingGraphImage from "./FarrowingGraphImage.component";
import DividerArrow from "../../DividerArrow/DividerArrow.component";

const FarrowingGraph = () => {

    const data = useStaticQuery(graphql`
        query {
            allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(farrowing)/"}, frontmatter: {title: {eq: "Farrowing Graph"}}}) {
                edges {
                    node {
                        frontmatter {
                            title
                            subtitle
                        }
                        html
                    }
                }
            }
        }
    `)
    
    return (
        <div>
            {
                data.allMarkdownRemark.edges.map(({node}) => (
                    <div className="farrowing-graph">
                        <DividerArrow />
                        <div className="farrowing-graph-text">
                            <TonisityLogo />
                            <h3 dangerouslySetInnerHTML={{ __html: node.frontmatter.subtitle }} />
                        </div>
                        <div className="farrowing-graph-image">
                            <FarrowingGraphImage />
                        </div>
                    </div>
                ))
            }
            
        </div>
    )
}

export default FarrowingGraph;