import React from "react"
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";

import Layout from "../components/layout"
import SEO from "../components/seo"

// Components
import FarrowingHeader from "../components/Farrowing/FarrowingHeader/FarrowingHeader.component";
import FarrowingGraph from "../components/Farrowing/FarrowingGraph/FarrowingGraph.component";
import DownloadAsset from "../components/DownloadAsset/DownloadAsset.component";
import VideoContainer from "../components/Farrowing/VideoContainer/VideoContainer.component";
import ContactUsComp from "../components/ContactUsComp/ContactUsComp.component";

const Farrowing = () => {

  const data = useStaticQuery(graphql`
    query {
      farrowingDownloads: allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(farrowing/downloads)/"}}) {
        edges {
          node {
            frontmatter {
              title
              mainTitle
              image
              englishLink
              spanishLink
            }
            html
          }
        }
      }
      farrowingPage: allMarkdownRemark(filter: {frontmatter: {title: {eq: "Farrowing"}}}) {
        edges {
          node {
            frontmatter {
              headerImage {
                childImageSharp {
                  fluid {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
          }
        }
      }
    }
  `)
  
  return (
    <Layout>
    <SEO title="Farrowing" />
    {
      data.farrowingPage.edges.map(({node}) => (
        <Img fluid={ node.frontmatter.headerImage.childImageSharp.fluid } />
      ))
    }
    {
      data.farrowingDownloads.edges.map(({node}) => (
        <div>
          <FarrowingHeader />
          <FarrowingGraph />
          <DownloadAsset 
            downloadTitle={node.frontmatter.title} 
            mainTitle={node.frontmatter.mainTitle} 
            body={node.html} 
            englishLink={node.frontmatter.englishLink}
            spanishLink={node.frontmatter.spanishLink}
          />
          <VideoContainer />
          <ContactUsComp />
        </div>
      ))
    }
    </Layout>
  )
}

export default Farrowing
