import React from "react";
import { graphql, useStaticQuery } from "gatsby";

// Styles
import "./VideoContainer.styles.scss";

// Components
import Video from "./Video.component";
import DividerArrow from "../../DividerArrow/DividerArrow.component";
import TonisityLogo from "../../TonisityLogo/TonisityLogo.component";

const VideoContainer = () => {

    const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(farrowing/videos)/"}}, sort: {fields: id}) {
        edges {
          node {
            frontmatter {
              title
              videoLink
            }
          }
        }
      }
    }
  `)
    
    return (
        <div className="video-container">
            <DividerArrow />
            {
                data.allMarkdownRemark.edges.map(({node}) => (
                    <div className="video-content">
                        <div className="video-text">
                            <TonisityLogo />
                            <h3>{ node.frontmatter.title }</h3>
                        </div>
                        <Video 
                            videoSrcURL={ node.frontmatter.videoLink }
                            videoTitle={ node.frontmatter.title }
                        />
                    </div>
                ))
            }
        </div>
    )
}

export default VideoContainer;